.slugdataimages {
  position: relative; }
  .slugdataimages img {
    margin: 5px; }
  .slugdataimages img:hover {
    cursor: pointer; }

.slugdataimages:hover .produt_hover_btn {
  display: block !important;
  position: absolute;
  top: -6px;
  right: 0;
  border: none;
  color: black;
  background-color: white; }

.slugdataimages:hover .delete12:hover {
  color: red;
  text-shadow: 0px 0px 10px red; }

.slugdataimages:hover .delete12:active {
  color: rgba(255, 0, 0, 0.507);
  text-shadow: 0px 0px 10px red; }

.btn-edit {
  background-color: #f3e13b; }

.btn {
  font-size: 14px; }

.ModalDelete1{
    width: 120vw;
    height: 100vh;
    position: fixed;
    top: -100%;
    left: -1%;
    /* transition: all 0.4s ease-in-out; */
    background-color: rgba(134, 129, 129, 0.453);
}
.ModalColumn{
    position: fixed;
    top: -100%;
    width: 40%;
    left: 30%;
    padding: 3% 2%;
    background-color: #fff;
    transition: all 0.4s ease-in-out;
    /* border: 1px solid rgba(0, 0, 0, 0.808); */
    box-shadow: 0 0 8px black;
    border-radius: 15px;
}
.ButtonsModalDelete{
    width: 30%;
    margin-left: 70%;
    display: flex;
    justify-content: space-around;
}
.Name-item{
    color: #be2d2d;
    font-weight: bolder;
}
.Price-item{
    font-weight: 700;
}
.row{
display: grid;
grid-template-columns: auto auto auto auto;
}
