.modal-dialog{
    max-width: 50vw !important;
}
.ModalBody{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.ModalBodyLeftHero, .ModalBodyRightHero{
    width: 45%;
}

.card12 h1{
    font-size: 50px;
}
.input-dollar{
    width: 20%;
}
.InputGroup{
    display: grid;
    grid-template-columns: 20% 20% 20% 20%;
    justify-content: space-between;
}
.input12{
    width: 100%;
    display: block;
}
select{
    background-color: #757575;
    font-size: 20px;
    display: block;
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    border-radius: 10px;
    color: #ffffff;
}
input {
    font-size: 20px;
    display: block;
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    background: none;
    background-image: none;
    border: 1px solid #a0b3b0;
    color: #ffffff;
    background: #757575;
    border-radius: 0;
    border-radius: 10px;
    transition: border-color 0.25s ease, box-shadow 0.25s ease;
    margin-bottom: 20px;
  }
  input:focus {
    outline: 0;
    border-color: #1ab188;
  }

